<template lang="pug">
  .start-screen.flex.flex-col.justify-center
    .start-screen-holder.bg-white.pb-8
      img(src="images/pattern.jpg")
      img.mx-auto.my-8(src="images/swimtap-logo.png")
      h4.text-center.pb-1.uppercase.tracking-wider Which Style?
      .flex.justify-center
        button.tracking-widest.text-xl.bg-blue-500.hover_bg-blue-700.text-white.font-bold.py-2.px-4.rounded-sm.border-solid.border.mr-2(@click="onStartGame('freestyle')" @mouseover="onBthHover") FREESTYLE
        button.tracking-widest.text-xl.bg-blue-500.hover_bg-blue-700.text-white.font-bold.py-2.px-4.rounded-sm.border-solid.border(@click="onStartGame('butterfly')" @mouseover="onBthHover") BUTTERFLY
    //- button.bg-blue-500.hover_bg-blue-700.text-white.font-bold.py-2.px-4.rounded(@click="onStartGame2") Start 2 player
</template>

<script>
import { mapActions, mapState } from 'vuex'
import GraphicsEngine from '@/helpers/GraphicsEngine';

import { TIMER_START,SET_STYLE } from '@/store/action-types'
import { gsap } from 'gsap'

export default {
  name: 'StartScreen',
  computed: {
    ...mapState({
      style: 'style',
      sound: 'sound',
    })
  },
  props: {
    msg: String
  },
  mounted() {
    gsap.from(this.$el, {opacity: 0, y: -100, duration: 0.4});
  },
  methods: {
    onStartGame (style) {
      this[SET_STYLE](style)
      gsap.to(this.$el, {opacity: 0, y: 100, duration: 0.4, onComplete: this.dispatchComplete });
      console.log('start click');
      if (this.sound) GraphicsEngine.loader.resources.soundClick.sound.play();
    },
    onStartGame2 () {
      gsap.to(this.$el, {opacity: 0, y: 100, duration: 0.4, onComplete: this.dispatchComplete, onCompleteParams:[true] });
      console.log('start click');
    },
    onBthHover() {
      // soundHover
      if (this.sound) GraphicsEngine.loader.resources.soundHover.sound.play();
    },
    dispatchComplete(twoPlayers = false) {
      this.$emit('outro-complete', {type: 'startScreen', value: twoPlayers})
    },
    ...mapActions([
      TIMER_START,
      SET_STYLE,
    ]),
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

  .start-screen {
    position: absolute;
    width: 100%;
    height: 100%;

    button {
      cursor: pointer;;
      border-color: white;
      border-width: 3px;
      transition: border 0.4s ease-out, background 0.4s ease-out;
      &:hover {
        border-color: #103665;
      }
    }
  }
</style>
