<template lang="pug">
  .game-screen()
    count-down.z-10.absolute(v-if="countDownShow" @outro-complete="countDownDone" :sound="sound")
    .game-screen-holder.flex.z-1
      .progress(v-if="swimStart && !countDownShow" :class="{ 'is-mobile':isMobile }")
        .bg
        .bar(:style="{'height':progressBar}")
      .timing(v-if="swimStart && !countDownShow" :class="{ 'is-mobile':isMobile }")
        h1.outline.absolute {{ displayTiming }}
        h1.absolute {{ displayTiming }}

      .distance.z-10.hidden(class="w-1/2")
        h1 Player 1
        h2 Timing : {{ displayTiming }}
        h2 Power : {{ player1Power }}
        h2 Distance : {{ player1Distance }}
      game-graphics(:distance="player1Distance" :swim-start="swimStart" :power="player1Power", :swim-end="swimEnd" :swim-style="swimStyle" ref="gameGraphics")
      game-msg(ref="gameMsg" :progress="progressRatio" :sound="sound")
      .mobile-controls.z-10.flex.justify-center.items-center(v-if="isMobile && (swimStart || countDownShow) && !swimEnd " :class="{ 'is-mobile':isMobile }")
        .left.m-4(@click.prevent="mobileBtEvent('left')")
          img(src="images/buttons/left-btn.jpg")
        .right.m-4(@click.prevent="mobileBtEvent('right')")
          img(src="images/buttons/right-btn.jpg")

</template>

<script>
  import { mapState, mapActions } from 'vuex'
  import { GAME_DONE, SET_GAME_TIMING } from '@/store/action-types'
  import GraphicsEngine from '@/helpers/GraphicsEngine';
  import CountDown from '@/components/CountDown.vue';
  import GameGraphics from '@/components/GameGraphics.vue';
  import GameMsg from '@/components/GameMsg.vue';
  import SecHelper from '@/helpers/SecondsFormater';
  import KeyPressEngine from '@/helpers/KeyPressEngine';

  export default {
    components: { CountDown, GameGraphics, GameMsg },
    props: [
      'begin'
    ],
    watch: {
      begin(newValue) {
        if (newValue) {
          this.countDownShow = true;
        }
      },
      countDownShow (newValue) {
        if (newValue) {
          this.$refs.gameGraphics.showMobileInterface();
        }
      },
      sound(newValue) {
        if (newValue) {
          if (this.swimStart && !this.swimEnd) GraphicsEngine.loader.resources.soundCrowd.sound.play({ loop: true });
          GraphicsEngine.loader.resources.soundCrowd.sound.volume = 0.3;
        } else {
          GraphicsEngine.loader.resources.soundCrowd.sound.stop( );
        }
      }
    },
    data() {
      return {
        startTime: 0,
        gameTickerInterval: null,
        gameFinishTickerInterval: null,
        timer: Object,
        countDownShow: false,
        swimStart: false,
        swimEnd: false,
        timing: 0,
        stats: new Array(2),
        player1KeyPressEngine: Object,
        player1Power: Number,
        player1Distance: Number,
        distance: 25,
        preEndRation: 0.1,
        endPower: 0,
      }
    },
    computed: {
      ...mapState({
        players: 'players',
        swimStyle: 'style',
        isMobile: 'isMobile',
        sound: 'sound',
      }),
      displayTiming() {
        return SecHelper(this.timing);
      },
      progressBar () {
        return `${this.player1Distance/this.distance * 100}%`
      },
      progressRatio () {
        return this.player1Distance/this.distance;
      }
    },
    methods: {
      ...mapActions([GAME_DONE, SET_GAME_TIMING]),
      countDownDone() {
        console.log('[GameScreen] - countDownDone()');
        this.countDownShow = false;
        this.swimStart = false;
        this.startGame();

        if (this.sound) GraphicsEngine.loader.resources.soundCrowd.sound.play({ loop: true });
        if (this.sound) GraphicsEngine.loader.resources.soundCrowd.sound.volume = 0.3;
      },
      startGame() {
        this.swimStart = true;
        this.swimEnd = false;
        this.startTime = new Date().getTime();
        if (this.gameTickerInterval) {
          clearInterval(this.gameTickerInterval);
        }
        this.gameTickerInterval = setInterval(this.gameTicker, 40);
        this.stats[0] = new KeyPressEngine(0);
        this.stats[0].start();
        if (this.players >=2) {
          this.stats[1] = new KeyPressEngine(1);
          this.stats[1].start();
        }
      },
      mobileBtEvent(tag) {
        if(this.stats[0]) this.stats[0].mobileBtEvent(tag);
      },
      gameTicker() {
        const currentTime = new Date().getTime();
        this.timing = currentTime - this.startTime;

        this.player1Power = this.stats[0].power;
        // console.log(`this.player1Power ${this.player1Power}`);

        // End game when too long
        if ( this.timing >= 60000) {
          this.endGame();
          return;
        }


        if (this.stats[0].distance >= this.distance * (1 - this.preEndRation) ) {
          this.finishSwim();
        }
        this.player1Distance = this.stats[0].distance;

        // if (this.players >=2) {
        //   this.player2Power = this.stats[1].power;
        //   if (this.stats[1].distance >= this.distance || this.timing > 60000) {
        //     this.endGame();
        //   }
        //   this.player2Distance = this.stats[1].distance;
        // }
      },
      finishSwim() {
        clearInterval(this.gameTickerInterval);
        this.gameTickerInterval = null;
        this.endPower = this.stats[0].power;
        // this.$refs.gameGraphics.finishSwim(this.endPower);

        if (this.gameFinishTickerInterval) {
          clearInterval(this.gameFinishTickerInterval);
          this.gameFinishTickerInterval = null;
        }
        // console.log('[finishSwimTicker]', this.$refs)
        this.gameFinishTickerInterval = setInterval(this.finishSwimTicker, 40)
      },
      finishSwimTicker () {
        this.$refs.gameGraphics.finishSwim(this.endPower);
        console.log('finishSwimTicker', this.$refs)
        if (this.$refs.gameGraphics.swimEndingDone) {
          clearInterval(this.gameFinishTickerInterval);
          this.gameFinishTickerInterval = null;
          this.endGame();

        }
      },
      endGame() {
        if (this.gameTickerInterval) clearInterval(this.gameTickerInterval);
        this[GAME_DONE]();
        this[SET_GAME_TIMING](this.timing);
        this.swimEnd = true;
        this.stats[0].stop();
        if (this.players >=2) {
          this.stats[1].stop();
        }

        GraphicsEngine.loader.resources.soundCrowd.sound.stop();
      },
      restartGame () {
        // this.gameTickerInterval = 
        // this.timer = 
        clearInterval(this.gameTickerInterval);
        this.gameTickerInterval = null;
        this.startTime = 0;
        this.countDownShow = false;
        this.swimStart = false;
        this.swimEnd = false;
        this.timing = 0;
        this.stats[0] 
        this.$nextTick(()=> {
          this.$refs.gameGraphics.restartGame();
        })
        this.$refs.gameMsg.reset();
        //  startTime: 0,
        // gameTickerInterval: null,
        // timer: Object,
        console.log('[GameScreen] - restart');
      }
    },
  }
</script>

