class KeyPressEngine {
  #player = 0;
  #keyCodes;
  #lastPress = 0;
  #timer;
  #power = 20;
  #distance = 0;

  constructor(player) {
    this.#player = player;
    if (player === 0) {
      this.#keyCodes = [65, 83];
    } else {
      this.#keyCodes = [79, 80];
    }
  }

  get power() {
    return this.#power;
  }
  get distance() {
    return this.#distance;
  }

  start() {
    document.addEventListener('keyup', event => {this.keyEvent(event)} );
    // console.log(this.#timer);
    this.#distance = 0;
    this.#timer= setInterval (()=> {this.intervalCallback()}, 40);
  }
  
  intervalCallback () {
    if (this.#power > 0 ) this.#power -= 1.9;
    if (this.#power < 0 ) this.#power = 0;
    this.#distance += this.#power/ 880; //480
    // console.log(this.#power);
  }

  stop() {
    document.removeEventListener('keyup', this.keyEvent );
  }


  keyEvent(event) {
    const keyPressed = event.keyCode; 
    // console.log('keyEvent', keyPressed, this);
    if (this.#lastPress === keyPressed) {
      if (this.#power > 0 ) this.#power -= 4;
      if (this.#power < 0 ) this.#power = 0;
      return;
    } 
    if (this.#keyCodes.indexOf(keyPressed)>=0) {
      if (this.#power <= 40) {this.#power += 6;}
      else {this.#power += 2}
      
      this.#lastPress = keyPressed;
    }

  }
  mobileBtEvent(bt) {
    const key = { keyCode: (bt == 'left')? 65: 83 };
    this.keyEvent(key);
  }

}

export default KeyPressEngine;