<template lang="pug">
  .instructions.flex.flex-col.justify-center
    .instructions-holder.bg-white.pb-8.text-center
      img.pb-8(src="images/pattern.jpg")
      h4.text-center.text-xl.pb-1.uppercase.tracking-widest how to play
      h5.text-center.text-md.tracking-wider.uppercase(v-if="!isMobile") Tap the keys ‘a’ and ‘s’ to swim
      h5.text-center.text-md.tracking-wider.uppercase(v-if="isMobile") Tap the ‘left’ and ‘right’ buttons swim
      img.py-8.mx-auto(src="images/instructions/desktop-instructions.gif" v-if="!isMobile")
      img.py-8.mx-auto(src="images/instructions/mobile-instructions.gif" v-if="isMobile")
      h5.text-center.text-md.tracking-wider.uppercase.pb-4 The faster you tap, the faster you swim!
      button.mx-auto.uppercase.tracking-widest.text-xl.bg-blue-500.hover_bg-blue-700.text-white.font-bold.py-2.px-4.rounded-sm.border-solid.border.mr-2(@click="onStartGame" @mouseover="onBthHover") Got It!

</template>

<script>
  import { mapState } from 'vuex'
  import { gsap } from 'gsap'
  import GraphicsEngine from '@/helpers/GraphicsEngine';


  export default {
    computed: {
      ...mapState({
        isMobile: 'isMobile',
        sound: 'sound',
      })
    },
    mounted() {
      gsap.from(this.$el, {opacity: 0, y: -100, duration: 0.4 });
    },
    methods: {
      onStartGame() {
        gsap.to(this.$el, {opacity: 0, y: 100, duration: 0.4, onComplete: this.dispatchComplete });
        if (this.sound) GraphicsEngine.loader.resources.soundClick.sound.play();
      },
      dispatchComplete() {
        this.$emit('outro-complete', {type: 'instructionScreen'})
      },
      onBthHover() {
        // soundHover
        if (this.sound) GraphicsEngine.loader.resources.soundHover.sound.play();
      },
      
    }
  }
</script>

<style lang="scss" scoped>

  .instructions {
    position: absolute;
    width: 100%;
    height: 100%;

    button {
      cursor: pointer;;
      border-color: white;
      border-width: 3px;
      transition: border 0.4s ease-out, background 0.4s ease-out;
      &:hover {
        border-color: #103665;
      }
    }
  }
</style>
