<template lang="pug">
  .game-graphics
</template>

<script>
  import { mapState } from 'vuex';
  import GraphicsEngine from '@/helpers/GraphicsEngine';

  export default {
    props: [
      'distance',
      'swimStart',
      'power',
      'swimEnd',
      'swimStyle',
      'mapState',
    ],
    watch: {
      distance(newValue) {
        this.ge.setProgress( newValue/this.poolDistance ) ;
      },
      swimStart(newValue) {
        if (newValue) {
          this.ge.startSwim();
        }
      },
      swimEnd () {
        this.ge.gameEnd();
        // if (finishSwim) this.ge.finishSwim();
      },
      power(newValue, oldValue) {
        if (!this.swimEnd) {
          if (newValue >0 ) {
            if(newValue != oldValue) this.ge.setSwimmerIdle(false);
          } else {
            if(newValue != oldValue) this.ge.setSwimmerIdle();
          }
          if(newValue != oldValue) {
            this.ge.setSwimSpeed(newValue)
          }
        }
      },
      swimStyle(newValue) {
        this.ge.style = newValue;
      }
    },
    data() {
      return {
        ge: Object,
        poolDistance: 25,
        swimEndingDone: false,
        // pixi: Object,
      }
    },
    computed: {
      ...mapState({ 
        isMobile : 'isMobile',
        sound: 'sound',
      }),
    },
    mounted() {
      console.log('GameGraphics.vue mounted');
      this.ge = new GraphicsEngine();
      this.ge.mobile = this.isMobile;
      this.ge.init(this.$el);
      // this.$el.appendChild(this.pixi.view);
    },
    methods: {
      restartGame() {
        this.ge.restart();
      },
      finishSwim (power) {
        this.ge.finishSwim(power);
        this.swimEndingDone = this.ge.swimEndingDone;
      },
      showMobileInterface () {
        this.ge.showMobileInterface();
      }
    }
  }
</script>

<style lang="scss" scoped>
  .game-graphics {
    position: absolute;
    width: 400px;
    height: 600px;
    top: 0;
    left: 0;
    z-index: 0;
  }
</style>
