import * as PIXI from 'pixi.js'
import 'pixi-sound';
import BezierEasing from 'bezier-easing'

const map = (value, x1, y1, x2, y2) => (value - x1) * (y2 - x2) / (y1 - x1) + x2;

class GraphicsEngine {
  static loader = new PIXI.Loader();
  #el;
  #pixi;
  #stage;
  #renderer;
  #style = 'freestyle';
  #viewWidth = 400;
  #viewHeight = 600;
  #ease = BezierEasing(0,.8,1,.2)
  #startRatio = 0.06;
  #endtRatio = 0.85;
  #waterSpeed = 1/60* 12; //12 fps
  #animationSpeed = 1/60* 24; //24 fps
  #isMobile = false;

  // Graphic Settings
  #swimDistance = 25;
  #poolMultiples = 8;
  #poolEdge = 66;
  #poolStart = 86;
  #poolLength = 600;
  #poolHeight = this.#viewHeight * this.#poolMultiples + this.#poolStart * 2 + this.#poolEdge;
  #poolInitialPosition = (this.#viewHeight - this.#poolEdge);
  // Graphic Sprites
  #pool;
  #float;
  #water;

  #swimmerContainer
  #swimmerStart
  #swimmerSwimming
  #swimmerSwimmingButterfly
  //swimmerButterfly
  #swimmerIdle

  #mobileInterface

  swimEndingDone = false;

  constructor() {
    
    // this.buildFloat();
  }
  init (element) {
    console.log('GraphicsEngine', GraphicsEngine.loader)
    this.#el = element;
    this.#pixi = new PIXI.Application({
      transparent: true,
      width: 400,
      height: this.#viewHeight,
    });
    this.#el.appendChild(this.#pixi.view);
    this.#stage = this.#pixi.stage;
    this.#renderer = PIXI.autoDetectRenderer(
      400,
      this.#viewHeight,
      {view:this.$el}
    );

    this.buildPool();
    this.buildSwimmer();
    this.buildMobileInterface();
  }

  set mobile (value) {
    this.#isMobile = value;
  }

  buildPool () {
    this.#pool = new PIXI.Container();
    // let edgeTexture = PIXI.Texture(GraphicsEngine.loader.resources.poolstart1.texture);
    // let edgeTexture2 = PIXI.Texture(GraphicsEngine.loader.resources.poolend1.texture);
    let poolEdge = new PIXI.Sprite(GraphicsEngine.loader.resources.poolstart1.texture);
    let poolEdgeEnd = new PIXI.Sprite(GraphicsEngine.loader.resources.poolend1.texture);

    // let poolOverlay = new PIXI.Sprite(GraphicsEngine.loader.resources.pooloverlay.texture);
    // poolEdgeEnd.angle = 180;
    // poolEdgeEnd.x = 400;

    // let startTexture = PIXI.Texture.from('images/pool/poolstart-2.jpg');
    // let startTexture2 = PIXI.Texture.from('images/pool/poolend-2.jpg');
    let poolStart = new PIXI.Sprite(GraphicsEngine.loader.resources.poolstart2.texture);
    let poolEnd = new PIXI.Sprite(GraphicsEngine.loader.resources.poolend2.texture);
    // poolEnd.angle = 180;
    // poolEnd.x = 400;
    poolStart.y = -this.#poolStart;

    const poolHeight = this.#viewHeight * this.#poolMultiples;
    let poolContainer = new PIXI.Container();
    // let poolTexture = PIXI.Texture.from('images/pool/poolmiddle.jpg');
    let pool = new PIXI.TilingSprite(GraphicsEngine.loader.resources.poolmiddle.texture, this.#viewWidth, poolHeight);
    poolContainer.y = 0 - poolHeight - this.#poolStart
    // pool.y = 0 - poolHeight - this.#poolStart;

    let poolOverlay = new PIXI.TilingSprite(GraphicsEngine.loader.resources.pooloverlay.texture, this.#viewWidth, poolHeight + this.#poolStart * 2);
    poolOverlay.y = 0 - poolHeight  - this.#poolStart* 2;

    // let floatTexture = PIXI.Texture.from('images/pool/poolmiddle-float.png');
    let floats = new PIXI.TilingSprite(GraphicsEngine.loader.resources.poolmiddlefloat.texture, this.#viewWidth, poolHeight + this.#poolStart * 2);
    floats.y = 0 - poolHeight  - this.#poolStart* 2;

    // pool.y = 0 - 2700;
    console.log(`poolHeight: ${poolHeight}`);
    poolContainer.addChild(pool);
    this.#pool.addChild(poolContainer);

    this.#water = new PIXI.Container();

    for (let waterIndex = 0; waterIndex < this.#poolMultiples; waterIndex++) {
      let waterAni = this.buildAnimatedSprite('water', 24);
      this.#water.addChild (waterAni)
      waterAni.animationSpeed = this.#waterSpeed;
      waterAni.y = waterIndex* (this.#viewHeight - 20);
      waterAni.play();
    }

    let waterAni = this.buildAnimatedSprite('water', 24);
    this.#water.addChild (waterAni)
    waterAni.animationSpeed = this.#waterSpeed;
    waterAni.y = this.#poolMultiples * (this.#viewHeight - 20);
    waterAni.play();

    poolEnd.y = 0 - poolHeight -  this.#poolStart * 2;
    poolEdgeEnd.y = 0 - poolHeight - this.#poolStart * 2 - this.#poolEdge - 0;
    // this.#pool.y = this.#poolHeight;
    this.#pool.y = this.#viewHeight - this.#poolEdge;
    this.#water.y = 0-poolHeight  -  this.#poolStart * 2 - 20;


    this.#pool.addChild(poolStart);
    this.#pool.addChild(poolEnd);
    this.#pool.addChild(this.#water);
    this.#pool.addChild(poolOverlay);
    this.#pool.addChild(floats);
    

    this.#pool.addChild(poolEdgeEnd);
    this.#pool.addChild(poolEdge);


    this.#stage.addChild(this.#pool);
    // this.#stage.addChild(poolOverlay);
    //
    // this.#water.play();
    console.log(`Animated Sprite animatedWater : ${this.#water.height} ${this.#water.currentFrame}`);
    // this.#pool.y = 300;
  }
  
  buildSwimmer() {
    this.#swimmerContainer =  new PIXI.Container();
    this.#swimmerStart = this.buildAnimatedSprite('swimmerStart', 8);
    this.#swimmerStart.loop = false;
    // this.#swimmerStart.y = - this.#poolEdge
    this.#swimmerStart.onComplete = () => { this.startSwimEnd() };
    this.#swimmerContainer.addChild (this.#swimmerStart);
    

    this.#swimmerSwimming = this.buildAnimatedSprite('swimmerFreestyle', 21);
    this.#swimmerSwimming.visible = false;
    this.#swimmerContainer.addChild (this.#swimmerSwimming);
    
    this.#swimmerSwimmingButterfly = this.buildAnimatedSprite('swimmerButterfly', 12);
    this.#swimmerSwimmingButterfly.visible = false;
    this.#swimmerContainer.addChild (this.#swimmerSwimmingButterfly);
    
    this.#swimmerIdle = this.buildAnimatedSprite('swimmerIdle', 19);
    this.#swimmerIdle.visible = false;
    this.#swimmerContainer.addChild (this.#swimmerIdle);
    

    this.#stage.addChild(this.#swimmerContainer);
    // waterAni.y = this.#poolMultiples * (this.#viewHeight - 20);
    // waterAni.play();
  }

  buildMobileInterface () {
    this.#mobileInterface = new PIXI.Container();
    const graphics = new PIXI.Graphics();
    const interfaceHeight = 145;

    // Rectangle
    graphics.beginFill(0xffffff);
    graphics.drawRect(0, 0, this.#viewWidth, interfaceHeight);
    graphics.endFill();
    this.#mobileInterface.addChild(graphics);
    this.#mobileInterface.visible = false;
    this.#mobileInterface.y = this.#viewHeight - interfaceHeight;
    this.#stage.addChild(this.#mobileInterface);
  }

  showMobileInterface () {
    if (this.#isMobile) {
      this.#mobileInterface.visible = true;
      this.#pool.y = this.#viewHeight - 145 - this.#poolEdge;
      // this.#swimmerStart.y = 0 - 145;
      this.#swimmerContainer.y = 0 - 145;
    }
  }

  startSwim() {
    if (this.#isMobile) {
      this.showMobileInterface();
      
    }
    this.#swimmerStart.play();
  }

  setSwimmerIdle (t = true) {
    // console.log(`GE setSwimmerIdle`);
    if (t) {
      this.#swimmerSwimming.visible = false;
      this.#swimmerSwimming.stop();
      this.#swimmerSwimmingButterfly.visible = false;
      this.#swimmerSwimmingButterfly.stop();
      this.#swimmerIdle.visible = true;
      this.#swimmerIdle.play();
    } else {
      if(this.#style === 'freestyle'){
        this.#swimmerSwimming.visible = true;
        this.#swimmerSwimming.play();
      } else {
        this.#swimmerSwimmingButterfly.visible = true;
        this.#swimmerSwimmingButterfly.play();
      }
      this.#swimmerIdle.visible = false;
      this.#swimmerIdle.stop();
      
    }
  }

  startSwimEnd() {
    this.#swimmerStart.visible = false;

    if(this.#style === 'freestyle'){
      this.#swimmerSwimming.visible = true;
      this.#swimmerSwimming.play();
    } else {
      this.#swimmerSwimmingButterfly.visible = true;
      this.#swimmerSwimmingButterfly.play();
    }
  }

  setProgress (progress) {
    const maxTravel = this.#poolHeight ;
    let poolY = map(progress, 0, 1-this.#startRatio, this.#poolInitialPosition, maxTravel + 145)
    // this.#pool.y = poolY + this.#poolInitialPosition;
    this.#pool.y = (poolY < this.#poolInitialPosition) ? this.#poolInitialPosition : (poolY > maxTravel) ? maxTravel : poolY;
    
    // if (progress >= 1-this.#startRatio) {
    //   //this.#lastPower
    //   if (this.#swimmerContainer.y <= 170) {
    //     this.#swimmerContainer.y += this.#lastPower;
    //   }
    // }
    
    // let swimmerY = map(progress, 1-this.#startRatio, 1, 0, 170);
    // if (swimmerY >0) {
    //   this.#swimmerContainer.y = 0-swimmerY;
    // } else {
    //   this.#swimmerContainer.y = 0;
    // }
    // console.log('swimmerY progress', progress, swimmerY);
    // console.log(`Animated Sprite animatedWater : ${this.#water.width} ${this.#water.currentFrame}`);
  }

  finishSwim(power) {
    
    if (this.#swimmerContainer.y <= 0-170) {
      this.swimEndingDone = true;
      this.setSwimmerIdle();
    } else {
      this.#swimmerContainer.y -= power;
      this.setSwimSpeed(power)
      // this.setProgress();
    }
    //#swimEnding
  }

  set style (style) {
    this.#style = style;
  }

  setSwimSpeed (power) {
    this.#swimmerSwimmingButterfly.animationSpeed = 1/60* 12 * (power/20);
    this.#swimmerSwimming.animationSpeed = 1/60* 21 * (power/20);
    // this.#lastPower = power;
  }

  gameEnd() {
    console.log('GE game end');
    this.#mobileInterface.visible = false;
    this.setSwimmerIdle();
  }

  buildAnimatedSprite (assetName, frameCount) {
    let textureArray = [];
    for (let index = 0; index < frameCount; index++) {
      textureArray.push(GraphicsEngine.loader.resources[`${assetName}${index +1}`].texture);
    }
    let animatedSprite = new PIXI.AnimatedSprite(textureArray);
    animatedSprite.animationSpeed = this.#animationSpeed;
    return animatedSprite;
  }

  restart () {
    this.setProgress(0);
    this.#swimmerSwimming.visible = false;
    this.#swimmerSwimmingButterfly.visible = false;
    this.#swimmerIdle.visible = false;
    this.#swimmerStart.gotoAndStop(0);
    this.#swimmerStart.visible = true;
    this.#mobileInterface.visible = false;
    this.swimEndingDone = false;
    this.#swimmerContainer.y = 0;
    // this.#swimmerStart.y = 0 ;
  }
}
export default GraphicsEngine;