<template lang="pug">
  #swim-game.font-sans
    preloader(v-if="preloader" ref="preloader")
    game-screen(v-if="assetsLoaded" :begin='gameBegin' ref="gameScreen")
    transition(name="component-fade" mode="out-in" appear
      )
      component(:is="currentScreen" @outro-complete="outroComplete" v-if="assetsLoaded")
    button.sound-bt(@click="toggleSound" :class="{'sound-off': !sound}")
    //- start-screen
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { gsap } from 'gsap'
// import * as PIXI from 'pixi.js'

import GraphicsEngine from '@/helpers/GraphicsEngine';

import { SET_PLAYERS, GAME_RESTART, SET_SOUND } from '@/store/action-types'
import StartScreen from './components/StartScreen.vue';
import Instructions from './components/Instructions.vue';
import GameScreen from './components/GameScreen.vue';
import EndScreen from './components/EndScreen.vue';
import Preloader from './components/Preloader.vue';

export default {
  name: 'App',
  components: {
    StartScreen,
    GameScreen,
    Preloader,
    EndScreen,
  },
  data() {
    return {
      currentScreenId: 0,
      // screenSeq: [ GameScreen],
      screenSeq: [StartScreen, Instructions, EndScreen],
      exitTransitionClass: 'animated bounceOut',
      assetsLoaded: false,
      preloader: true,
      gameBegin: false,
    }
  },
  computed: {
    ...mapState({
      timerStarted: 'timerStarted',
      timerDone: 'timerDone',
      gameDone: 'gameDone',
      sound: 'sound',
    }),
    currentScreen() {
      if (!this.assetsLoaded) return;
      if ( !this.gameDone && this.screenSeq[this.currentScreenId] === EndScreen) return;
      return this.screenSeq[this.currentScreenId]
    },
    gameState() {
      return '';
    }
  },
  watch: {
  },
  mounted() {
    this.preloadImageAssets();

  },
  methods: {
    ...mapActions([
      SET_PLAYERS,
      GAME_RESTART,
      SET_SOUND,
    ]),
    outroComplete(value) {
      // console.log('app outroComplete', value);
      if (value.type === 'startScreen') {
        if (value.value) {
          this[SET_PLAYERS](2)
        } else {
          this[SET_PLAYERS](1)
        }
        this.currentScreenId ++;
      } else if ( value.type === 'instructionScreen' ) {
        this.gameBegin = true;
        this.currentScreenId ++;
      } else if (value.type === 'endScreen') {
        if (value.value === 'try-again') {
          this.gameBegin = false;
          this.currentScreenId = 0;
          console.log(`this.$refs`, this.$refs);
          this[GAME_RESTART]();
          this.$refs.gameScreen.restartGame();
        }
      }
    },
    preloadImageAssets() {
      let loader = GraphicsEngine.loader;
      loader.add('poolstart1', 'images/pool/poolstart-1.jpg')
      loader.add('poolstart2', 'images/pool/poolstart-2.jpg')
      loader.add('poolend1', 'images/pool/poolend-1.jpg')
      loader.add('poolend2', 'images/pool/poolend-2.jpg')
      loader.add('poolmiddle', 'images/pool/poolmiddle.jpg')
      loader.add('pooloverlay', 'images/pool/overlay.png')
      loader.add('poolmiddlefloat', 'images/pool/poolmiddle-float.png')
      
      loader.add('msgMid0', 'images/msg/a-doinggreat.png')
      loader.add('msgMid1', 'images/msg/a-greatstrokes.png')
      loader.add('msgMid2', 'images/msg/a-halfwaythere.png')
      loader.add('msgEnd0', 'images/msg/b-almostthere.png')
      loader.add('msgEnd1', 'images/msg/b-yougotthis.png')


      for (let index = 0; index < 24; index++) {
        loader.add(`water${index+1}`, `images/pool/water/water${index +1}.png`)
      }
      for (let index = 0; index < 8; index++) {
        loader.add(`swimmerStart${index+1}`, `images/swimming/startjump/startjump${index +1}.png`)
      }
      for (let index = 0; index < 21; index++) {
        loader.add(`swimmerFreestyle${index+1}`, `images/swimming/freestyle/freestyle${index +1}.png`)
      }
      for (let index = 0; index < 12; index++) {
        loader.add(`swimmerButterfly${index+1}`, `images/swimming/butterfly/butterfly${index +1}.png`)
      }
      for (let index = 0; index < 19; index++) {
        loader.add(`swimmerIdle${index+1}`, `images/swimming/idle/idle${index +1}.png`)
      }

      loader.add('soundCount', 'sounds/countdown-1.mp3');
      loader.add('soundSplashStart', 'sounds/splash.mp3');
      loader.add('soundPopup', 'sounds/pop-up.mp3');
      loader.add('soundHover', 'sounds/button-hover.mp3');
      loader.add('soundClick', 'sounds/button-click.mp3');
      loader.add('soundSwimming', 'sounds/swimming.mp3');
      loader.add('soundCrowd', 'sounds/crowd.mp3');


      loader.load(this.onLoadComplete);
    },
    onLoadComplete() {
      // this.assetsLoaded = true;
      console.log(this.$refs);
      this.assetsLoaded = true

      gsap.to(this.$refs.preloader.$el, {opacity: 0, duration: 1, 
        delay: 1,
        onComplete: () =>{
          this.preloader = false;
        }
      });
      GraphicsEngine.loader.resources.soundSwimming.sound.volume = 0.5
      GraphicsEngine.loader.resources.soundSwimming.sound.play({loop: true});
    },
    toggleSound() {
      if (this.sound) {

        GraphicsEngine.loader.resources.soundSwimming.sound.stop();
      } else {

        GraphicsEngine.loader.resources.soundSwimming.sound.play({loop: true});
      }
      this[SET_SOUND](!this.sound);
    }
  }
}
</script>


<style lang="scss">


//<link type="text/css" rel="stylesheet" href="//fast.fonts.net/cssapi/da049818-19ef-4b6f-a8e6-855df8f54f1b.css"/>
@tailwind base; /* Preflight will be injected here */
@tailwind components;
@tailwind utilities;

#swim-game {
  background-color: $color-backgroud;
  width: $main-width;
  height: $main-height;
  position: absolute;
  overflow: hidden;

  .sound-bt {
    width: 28px;
    height: 27px;
    position: absolute;
    z-index: 100001;
    top: 16px;
    right: 16px;
    background-image: url(../public/images/buttons/sound-bt.png);
    background-repeat: no-repeat;
    background-size: 56px 27px;
    
    &:hover {
      background-position-x: -28px;
    }

    &:focus {
      outline: none;
    }
    &.sound-off {
      background-position-x: -28px;

      &:hover {
        background-position-x: 0px;
      }
    }
  }

}
</style>
