<template lang="pug">
  .game-msg
    .game-msg-holder.absolute.w-full.bottom-0(:class=" {'pb-72': isMobile, 'pb-32': !isMobile } ")
      img.img-msg.mx-auto(:src="imagePath" style="opacity:0")
      //17.5rem
</template>

<script>
  import { mapState } from 'vuex';
  import { gsap } from 'gsap';
  import GraphicsEngine from '@/helpers/GraphicsEngine';

  export default {
    props: [
      'progress',
      'sound'
    ],
    watch: {
      progress(newValue) {
        if (newValue >0.45 && !this.msgMidShown) {
          this.msgMidShown = true;
          this.showMsg(0)
        }
        if (newValue >0.7 && !this.msgEndShown) {
          this.msgEndShown = true;
          this.showMsg(1)
        }
      }
    },
    computed: {
      ...mapState({
        isMobile: 'isMobile',
      }),
      imagePath() {
        return this.images[this.msgGroupToShow][this.msgToShow];
      },
    },
    data() {
      return {
        msgGroupToShow: 0,
        msgToShow: 0,
        msgMidShown: false,
        msgEndShown: false,
        images: [
          [
            'images/msg/a-doinggreat.png',
            'images/msg/a-greatstrokes.png',
            'images/msg/a-halfwaythere.png',
          ],
          [
            'images/msg/b-almostthere.png',
            'images/msg/b-yougotthis.png',
          ]
        ]
      }
    },
    mounted() {
      // const imgTarget = this.$el.getElementsByClassName('img-msg');
    },
    methods: {
      showMsg(tag) {
        //progress
        const imgSet = this.images[tag]
        this.msgGroupToShow = tag;
        const randImageID = this.getRandomIntInclusive(0, imgSet.length-1);
        this.msgToShow = randImageID;
        let tl = gsap.timeline({repeat: 0});

        const imgTarget = this.$el.getElementsByClassName('img-msg');
        tl.fromTo(imgTarget, {opacity: 0, scale:1.7}, {opacity: 1, scale:1, duration: 1.2, ease:"elastic.out(2, 0.5)" } );
        tl.to(imgTarget, {opacity: 0, scale:1.2, duration: 0.6, ease:"expo.out" }, "+=1");

        if (this.sound) GraphicsEngine.loader.resources.soundPopup.sound.play();
      },
      getRandomIntInclusive(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min;
      },
      reset() {
        this.msgMidShown = false;
        this.msgEndShown = false;
      }
    },
  }
</script>

<style lang="scss" scoped>
  .game-msg{
    position: absolute;
    width: 400px;
    height: 600px;
    top: 0;
    left: 0;
    z-index: 0;

    .pb-72 {
      padding-bottom: 17.5em;
    }
  }
</style>
