<template lang="pug">
  .count-down.flex.flex-col.justify-center
    .absolute.flex.justify-center.w-full.h-full.pb-16.counter-3
      img.self-center(src="images/3.png")
    .absolute.flex.justify-center.w-full.h-full.pb-16.counter-2
      img.self-center(src="images/2.png")
    .absolute.flex.justify-center.w-full.h-full.pb-16.counter-1
      img.self-center(src="images/1.png")
    .absolute.flex.justify-center.w-full.h-full.pb-16.counter-0
      img.self-center(src="images/go.png")
</template>

<script>
  import { mapActions } from 'vuex'
  // import Timer from 'easytimer.js';
  import { gsap } from 'gsap';
  import GraphicsEngine from '@/helpers/GraphicsEngine';

  import { TIMER_DONE } from '@/store/action-types';

  export default {
    props: [ 'sound' ],
    data() {
      return {
        timer: null,
        currentTime: 3,
      }
    },
    mounted() {
      // this.timer = new Timer();
      // this.timer.start({countdown: true, startValues: {seconds: 3}});
      // this.timer.addEventListener('secondsUpdated', 
      //   () => {  
      //     if (this.timer.getTimeValues().seconds === 0) {
      //       this.timerDone();
      //       return;
      //     } 
      //     this.currentTime = this.timer.getTimeValues().seconds 
      //   });
      //scale
      let tl = gsap.timeline({repeat: 0});
      tl.from(this.$el.getElementsByClassName('counter-3'), {opacity: 0, scale:1.7, duration: 1, ease:"elastic.out(2, 0.5)" });
      tl.to(this.$el.getElementsByClassName('counter-3'), {opacity: 0, scale:0.6, duration: 0.6, ease:"elastic.out(2, 0.5)" });
      tl.from(this.$el.getElementsByClassName('counter-2'), {opacity: 0, scale:1.7, duration: 1, ease:"elastic.out(2, 0.5)" }, "-=.6");
      tl.to(this.$el.getElementsByClassName('counter-2'), {opacity: 0, scale:0.6, duration: 0.6, ease:"elastic.out(2, 0.5)" });
      tl.from(this.$el.getElementsByClassName('counter-1'), {opacity: 0, scale:1.7, duration: 1, ease:"elastic.out(2, 0.5)" }, "-=.6");
      tl.to(this.$el.getElementsByClassName('counter-1'), {opacity: 0, scale:0.6, duration: 0.6, ease:"elastic.out(2, 0.5)", onComplete:()=>{
        if (this.sound) GraphicsEngine.loader.resources.soundSplashStart.sound.play();
      } });
      tl.from(this.$el.getElementsByClassName('counter-0'), {opacity: 0, scale:1.7, duration: 0.6, ease:"elastic.out(2, 0.5)" , onComplete:this.dispatchComplete }, "-=.6");
      tl.to(this.$el.getElementsByClassName('counter-0'), {opacity: 0, scale:0.6, duration: 0.6, ease:"elastic.out"});
      
      if (this.sound)  GraphicsEngine.loader.resources.soundCount.sound.play();
      // gsap.from(this.$el, {opacity: 0, scale:1.7, duration: 0.8, ease:"elastic.out(2, 0.5)" });
    },
    methods: {
      ...mapActions([
        TIMER_DONE
      ]),
      timerDone() {
        // gsap.to(this.$el, { scale:0, duration: 1, ease:"elastic.in(1, 0.3)", onComplete: this.dispatchComplete });
        
      },
      dispatchComplete() {
        this.$emit('outro-complete')
      },
    }
  }
</script>

<style lang="scss" scoped>
  .count-down {
    width: 100%;
    height: 100%;
    position: absolute;

    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
</style>
