<template lang="pug">
  .end-screen.flex.flex-col.justify-center
    .end-screen-holder.bg-white.pb-8.text-center
      img.pb-8(src="images/pattern.jpg")
      .flex.relative
        .col.relative(class="w-1/2").flex.align-center
          .throphy.m-auto
            img.mx-auto.mb-2(v-if="displayBadge" :src="badgeURL")
            h1.text-lg.uppercase.badge-text(v-if="displayBadge" :class="cssBadgeClass") You Achieved {{badge}}!
            .try-harder(v-if="!displayBadge")
              h1.text-3xl You Can Do Better!
        .col.relative(class="w-1/2")
          .timing.h-12.relative
            h1.outline.absolute {{ displayTiming }}
            h1.absolute {{ displayTiming }}
          
          h2.text-xl.mb-8 Impressive!
          h3.text-lg Share Your Achievement Badge!
          .col.mt-2
            social-sharing( :url="shareUrl"
              title="The Progressive JavaScript Framework"
              description="Intuitive, Fast and Composable MVVM for building interactive interfaces."
              
              inline-template )
              network(network="facebook")
                .social-icon.w-12.h-12.flex.items-center.mx-auto.my-auto
                  font-awesome-icon.m-auto(:icon="['fab', 'facebook-square']") Facebook
          button.mx-auto.uppercase.tracking-widest.text-xl.bg-blue-500.hover_bg-blue-700.text-white.font-bold.py-2.px-4.rounded-sm.border-solid.border.mr-2.mt-6(@click="onTryAgain" @mouseover="onBthHover") 
            | Play Again
            font-awesome-icon.m-auto.ml-1.pl-1(:icon="['fas', 'redo-alt']")

</template>

<script>
  import { gsap } from 'gsap'
  import { mapState } from 'vuex'

  // import { GAME_DONE } from '@/store/action-types'
  import GraphicsEngine from '@/helpers/GraphicsEngine';
  import SecHelper from '@/helpers/SecondsFormater';

  export default {
    data() {
      return {
        baseUrl: '',
      }
    },
    mounted() {
      gsap.from(this.$el, {opacity: 0, y: -100, duration: 0.4 });
      this.baseUrl = document.getElementById("swim-game-url").dataset.baseurl;
    },
    computed: {
      ...mapState({
        timing: 'gameTiming',
        style: 'style',
        sound: 'sound',
      }),
      displayTiming() {
        return SecHelper(this.timing);
      },
      displayBadge() {
        return (this.timing <= 30000);
      },
      badge() {
        let position = "";
        if(this.timing <= 30000) { // 25s
          position = 'bronze';
        } 
        if ( this.timing <= 25000 ) { // 23s
          position = 'silver';
        } 
        if (this.timing <= 20000){ //21s
          position = 'gold';
        } 
        return position;
      },
      badgeURL() {
        let position = "";
        if(this.timing <= 30000) { // 25s
          position = 'bronze';
        } 
        if ( this.timing <= 25000 ) { // 23s
          position = 'silver';
        } 
        if (this.timing <= 20000){ //21s
          position = 'gold';
        } 
        let url = `images/badges/${this.style}-${position}.png`;
        return url;
      },
      cssBadgeClass() {
        return {
          'gold': this.badge == 'gold',
          'silver': this.badge == 'silver',
          'bronze': this.badge == 'bronze',
        }
      },
      shareUrl() {
        const position = this.badge;
        return `${this.baseUrl}/?style=${this.style}&position=${position}`
      }
    },
    methods: {
      onStartGame() {
        gsap.to(this.$el, {opacity: 0, y: 100, duration: 0.4, onComplete: this.dispatchComplete });
      },
      dispatchComplete() {
        this.$emit('outro-complete', {type: 'endScreen'})
      },
      onTryAgain() {
        this.$emit('outro-complete', {type: 'endScreen', value:'try-again'})
        if (this.sound) GraphicsEngine.loader.resources.soundClick.sound.play();
      },
      onBthHover() {
        // soundHover
        if (this.sound) GraphicsEngine.loader.resources.soundHover.sound.play();
      },
    }
  }
</script>

