import Vue from 'vue'
import Vuex from 'vuex'
import MobileDetect from 'mobile-detect'

const md = new MobileDetect(window.navigator.userAgent);

import { TIMER_START, TIMER_DONE, SET_PLAYERS, SET_STYLE, GAME_DONE, SET_GAME_TIMING, GAME_RESTART, SET_SOUND } from './action-types'
import { TIMER_START_MUTATION, TIMER_DONE_MUTATION, SET_PLAYERS_MUTATION, SET_STYLE_MUTATION, GAME_DONE_MUTATION, SET_GAME_TIMING_MUTATION , SET_SOUND_MUTATION} from './mutation-types'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    timerStarted: false,
    timerDone: false,
    players: 1,
    style: 'freestyle',
    gameDone: false,
    gameStart: false,
    gameTiming: 0,
    isMobile: md.mobile(),
    sound: true,
  },
  mutations: {
    [TIMER_START_MUTATION] (state, payload) {
      state.timerStarted = payload;
    },
    [TIMER_DONE_MUTATION] (state , payload) {
      state.timerDone = payload;
    },
    [SET_PLAYERS_MUTATION] (state, payload) {
      // console.log('vuex SET_PLAYERS_MUTATION', payload);
      state.players = payload;
    },
    [SET_STYLE_MUTATION] (state, payload) {
      // console.log('vuex SET_PLAYERS_MUTATION', payload);
      state.style = payload;
    },
    [GAME_DONE_MUTATION] (state, payload) {
      // console.log('vuex SET_PLAYERS_MUTATION', payload);
      state.gameDone = payload;
    },
    [SET_GAME_TIMING_MUTATION] (state, payload) {
      // console.log('vuex SET_PLAYERS_MUTATION', payload);
      state.gameTiming = payload;
    },
    [SET_SOUND_MUTATION] (state, payload) {
      // console.log('vuex SET_PLAYERS_MUTATION', payload);
      state.sound = payload;
    },
  },
  actions: {
    [TIMER_START] (context, value = true) {
      context.commit(TIMER_START_MUTATION, value)
    },
    [TIMER_DONE] (context, value = true) {
      context.commit(TIMER_DONE_MUTATION, value)
    },
    [SET_PLAYERS] (context, value) {
      context.commit(SET_PLAYERS_MUTATION, value)
    },
    [SET_STYLE] (context, value) {
      context.commit(SET_STYLE_MUTATION, value)
    },
    [GAME_DONE] (context, value = true) {
      context.commit(GAME_DONE_MUTATION, value)
    },
    [SET_GAME_TIMING] (context, value) {
      context.commit(SET_GAME_TIMING_MUTATION, value)
    },
    [SET_SOUND] (context, value) {
      context.commit(SET_SOUND_MUTATION, value)
    },
    [GAME_RESTART] (context) {
      context.commit(TIMER_START_MUTATION, false)
      context.commit(TIMER_DONE_MUTATION, false)
      context.commit(GAME_DONE_MUTATION, false)
      context.commit(SET_GAME_TIMING_MUTATION, 0)
      // context.commit(GAME_RESTART_MUTATION)
    },
  },
  modules: {
  }
})
